.header {
  margin: 0 auto;
  padding: 45px 0 41px 0;
  max-width: 880px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 1024px) {
  .header {
    padding: 28px 30px 31.6px 30px;
  }
}
